import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: "/",
    name: "home",
    components: {
      desktop: () => import('../views/desktop/Home.vue'),
    }
  },
  {
    path: '/dex',
    name: 'explore',
    components:{
      desktop: () => import('../views/desktop/Explore.vue')
    } 
  },
  {
    path:"/dex/:tier",
    name:"tier",
    components:{
      desktop: () => import('../views/desktop/Tier.vue'),
    }     
  },
  {
    path:"/pokemon/:tier/:pokemon",
    name:"pokemon",
    components: {
      desktop: () => import('../views/desktop/Pokemon.vue')
    }
  },
  {
    path: "/battlepark",
    name: "battlepark",
    components:{
      desktop: () => import("../views/desktop/BattlePark.vue")
    } 
  },
  {
    path: "/battlepark/:tier/:id",
    name: "sharemyteam",
    components: {
      desktop: ()=>import("../views/desktop/ShareMyTeam.vue")
    }
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
