
import typeChart from "@/services/typeChart.js"
const statAlphabet = {
    HP: 0,
    ATTACK: 1,
    DEF: 2,
    SPA: 3,
    SPD: 4,
    SPE: 5
}

const natureMap = {
    LONELY: { increase: statAlphabet.ATTACK, decrease: statAlphabet.DEF },
    ADAMANT: { increase: statAlphabet.ATTACK, decrease: statAlphabet.SPA },
    NAUGHTY: { increase: statAlphabet.ATTACK, decrease: statAlphabet.SPD },
    BRAVE: { increase: statAlphabet.ATTACK, decrease: statAlphabet.SPE },
    BOLD: { increase: statAlphabet.DEF, decrease: statAlphabet.ATTACK },
    IMPISH: { increase: statAlphabet.DEF, decrease: statAlphabet.SPA },
    LAX: { increase: statAlphabet.DEF, decrease: statAlphabet.SPD },
    RELAXED: { increase: statAlphabet.DEF, decrease: statAlphabet.SPE },
    MODEST: { increase: statAlphabet.SPA, decrease: statAlphabet.ATTACK },
    MILD: { increase: statAlphabet.SPA, decrease: statAlphabet.DEF },
    RASH: { increase: statAlphabet.SPA, decrease: statAlphabet.SPD },
    QUIET: { increase: statAlphabet.SPA, decrease: statAlphabet.SPE },
    CALM: { increase: statAlphabet.SPD, decrease: statAlphabet.ATTACK },
    GENTLE: { increase: statAlphabet.SPD, decrease: statAlphabet.DEF },
    CAREFUL: { increase: statAlphabet.SPD, decrease: statAlphabet.SPA },
    SASSY: { increase: statAlphabet.SPD, decrease: statAlphabet.SPE },
    TIMID: { increase: statAlphabet.SPE, decrease: statAlphabet.ATTACK },
    HASTY: { increase: statAlphabet.SPE, decrease: statAlphabet.DEF },
    JOLLY: { increase: statAlphabet.SPE, decrease: statAlphabet.SPA },
    NAIVE: { increase: statAlphabet.SPE, decrease: statAlphabet.SPD },
}

const outputDamageMod = {
    lifeorb:1.3
}

const itemModStatPower = {
    choiceband: 1.5,
    choicespecs: 1.5
}

const damageReducingAbilities = {
    "Shadow Shield":0.5,
    "Multiscale": 0.5,
    "Filter": 0.75,
    "Solid Rock": 0.75
}

const STAB = function (moveType, pkmnType) {
    if (pkmnType.includes(moveType)) return 1.5
    return 1
}

const effectiveness = function (moveType, pkmnType) {
    let mod = 1
    for (const typing of pkmnType) {
        if (moveType in typeChart[typing].weakTo) {
            mod *= typeChart[typing].weakTo[moveType]
        }
        else if (moveType in typeChart[typing].resists) {
            mod *= typeChart[typing].resists[moveType]
        }
    }

    return mod
}

const classMap = {
    status: false,
    physical: true,
    special: true
}

const levelMap = {
    bss: 50,
    vgc: 50,
    lc: 5,
    uu: 100,
    ubers: 100,
    ou: 100,
    nfe: 100,
    mono: 100,
    ru: 100,
}

function maxHP(stats, spread, level) {
    return Math.floor(((2 * stats[0] + 31 + Math.floor(spread[0] / 4)) * level) / 100) + level + 10
}

function maxStat(stats, spread, level, statIndex) {
    let base = 0
    base = Math.floor((Math.floor(((2 * stats[statIndex] + 31 + Math.floor(spread[statIndex] / 4)) * level) / 100) + 5) * 1)
    return base
}

function calculateStats(pokemon, level) {
    let stats = [maxHP(pokemon.stats, pokemon.spread.stats, level)]
    for (let i = 1; i <= 5; i++) {
        stats.push(maxStat(pokemon.stats, pokemon.spread.stats, level, i))
    }
    const natureMod = natureMap[pokemon.spread.nature.toUpperCase()]
    if (natureMod !== undefined) {
        stats[natureMod.increase] = Math.floor(stats[natureMod.increase] * 1.1);
        stats[natureMod.decrease] = Math.floor(stats[natureMod.decrease] * 0.9);
    }
    return stats
}

function modDamagePower(move){
    let p = Number(move.power)
    return p
}

function damageCalculator(target, targeter, move, level) {
    let AD = 0
    switch (move.category) {
        case "special": {
            let m = 1
            if(itemModStatPower[targeter.item]){
                m = itemModStatPower[targeter.item]
            }
            AD = (targeter.stats[3]*m) / target.stats[4]
        } break;
        case "physical": {
            let m = 1
            if(itemModStatPower[targeter.item]){
                m = itemModStatPower[targeter.item]
            }
            AD = (targeter.stats[1]*m) / target.stats[2]
        } break;
    }
    const stab = STAB(move.type, targeter.types)
    const typeMatchup = effectiveness(move.type, target.types)
    const power = modDamagePower(move)
    let damage = ((((((2 * level) / 5) + 2) * power * AD) / 50) + 2) * 1 * stab * typeMatchup
    
    if(outputDamageMod[targeter.item]) damage = damage * outputDamageMod[targeter.item]
    if(damageReducingAbilities[target.ability]) damage *= damageReducingAbilities[target.ability]

    return ((damage / target.stats[0]) * 100).toFixed(1)
}


