export const Calculator = function (tier, pokemon, fetchedData, total) {
    // initialize data
    const level = levelMap[tier]
    const target = {
        stats: calculateStats(pokemon, level),
        types: pokemon.types,
        ability: pokemon.abilities[0][0].name
    }

    let battleResults = []

    for (let p in fetchedData) {
        // check if attacker has already been added to overall calculations
        if (total[p] === undefined) total[p] = {}

        // checks and counters loop 
        if (fetchedData[p].spreads !== undefined) {
            // select the most popular spread to run our calculations with
            fetchedData[p].spread = fetchedData[p].spreads[0][0]
            const targeter = {
                stats: calculateStats(fetchedData[p], level),
                types: fetchedData[p].types,
                item: fetchedData[p].items[0].slug,
            }

            // moves loop and battle calculations
            for (const move of fetchedData[p].moves) {

                if (classMap[move.category]) {
                    // check if move has already been added to overall calculations
                    if (total[p][move.name] === undefined) total[p][move.name] = {}
                    
                    let damageTaken = damageCalculator(target, targeter, move, level);
                    battleResults.push({
                        damageTaken: damageTaken,
                        attacker: p,
                        move: move.name,
                        nature: fetchedData[p].spread.nature,
                        spread: fetchedData[p].spread.stats,
                        item: fetchedData[p].items[0].slug
                    })
                    
                    if(damageTaken>100) damageTaken = 100
                    total[p][move.name] = {
                        damageTaken: ((Number(damageTaken)/6) + Number((total[p][move.name].damageTaken || 0))),    
                        attacker: p,
                        move: move.name,
                        nature: fetchedData[p].spread.nature,
                        spread: fetchedData[p].spread.stats,
                        item: fetchedData[p].items[0].slug
                    }
                }
            }
        }
    }

    return battleResults.sort((a, b) => {
        return b.damageTaken - a.damageTaken
    })
}
