export default {
    data(){
        return{
            dex: {
                'ou': 100,
                'uu': 100,
                'nu': 100,
                'ru': 100,
                'ubers': 100,
                'vgc': 50,
                'bss': 50,
                'monotype': 100,
                'lc': 5,
                'nfe': 100
            }
        }
    },
    computed:{
        battledex(){
            return this.$store.getters['dex/getBattledex']
        },
        battledex_size(){
            return Object.keys(this.$store.getters['dex/getBattledex']).length
        },
        getSelectedPokemon(){
            return this.$store.getters['dex/getSelectedPokemon']
        },
        isLoading(){
            return this.$store.getters['dex/isLoading']
        },
        isDexLoaded(){
            return this.$store.getters['dex/isBattleDexAvailable']
        }
    },
    methods:{
        select(from, what){
            let s = this.$store.getters[`dex/getSelectFromBattledex`](what)
            if (s !== undefined) {
                s.key = what;
                return s
            }
            return {slug: what}
        },
        setSelectedPokemon(pokemon){
            let pkmn = this.select('battledex', pokemon)
            this.$store.commit("dex/SET_SELECTED_POKEMON", pkmn)
        },
        unsetSelectedPokemon(){
            this.$store.commit("dex/SET_SELECTED_POKEMON", false)
        },
        getDefaultTierLevel(tier){
            return this.dex[tier]
        }
    }
}