const getSelectedPokemon = function (state) {
    return state.selectedPokemon
}

const isBattleDexAvailable = function (state) {
    if (state.battledex != false) {
        return true
    }
    return false
}

const getBattledex = function (state) {
    return state.battledex
}

const getSelectFromBattledex = function (state) {
    return key => {
        return state.battledex[key]
    }
}

const isLoading = function (state) {
    return state.isLoading
}

const getTheme = function(state) {
    return state.theme
}

export default {
    getTheme,
    getBattledex,
    getSelectedPokemon,
    getSelectFromBattledex,
    isBattleDexAvailable,
    isLoading
}