const getBattleResults = function(state){
    return state.battleResults
}

const getBattleResultsByKey = function(state){
    return (key) => {
        if(key !== -1){
            return state.battleResults[key]
        }
        return state.overall.battleResults
    }
} 

const getBattleSizeByKey = function(state){
    return (key) => {
        if(key !== -1) {
            return state.battleSize
        }
        return state.overall.battleSize
    }
}

const getBattleSize = function(state){
    return state.battleSize
}

const getCustomizedBattleResults = function(state){
    return state.customizedBattleResults
}

const getSelectedAllyPokemon = function(state){
    return state.selectedAllyPokemon
}

export default {
    getBattleResults,
    getBattleSize,
    getCustomizedBattleResults,
    getSelectedAllyPokemon,
    getBattleResultsByKey,
    getBattleSizeByKey
}